import { useState } from 'react';
import { getDateDetail, zfill } from './functools.js';

export const Article = (props) => {
  const style = props.style;
  const author = props.author;
  const raw_date = props.date;
  const content = props.content;

  const [isAuthor, setIsAuthor] = useState(false);
  let date_detail = getDateDetail(raw_date);

  date_detail = date_detail.map((element) => zfill(element));
  console.log(date_detail);
  const [ YEAR, MONTH, DAY, HOUR, MINUTE, SECOND ] = date_detail;

  return (
    <article className={style.content}>
      <h3 className={style.author}>{ author }</h3>

      <span className={style.date}>{ `${YEAR}-${MONTH}-${DAY} ${HOUR}:${MINUTE}:${SECOND}` }</span>

      <p className={style.text}>{ content }</p>

      <div className={style.buttons}>
        <button className={style.button} onClick={() => setIsAuthor(prevState => !prevState)}>詳細</button>
        {isAuthor ?
          <>
            <button className={style.button}>編集</button>
            <button className={style.button}>削除</button>
          </>
          :
          null
        }
      </div>
    </article>
  )
}